import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import Pagination from '../Base/Paginations/Pagnations';
import { getAllTickets } from '../Apis';

//import usersData from './UsersData'
const addticket = 'ticket/add'


function TicketRow(props) {
  const tickets = props.tickets
  const ticketLink = `/tickets/${tickets.id}`
  const updateticket = `ticket/update/${tickets.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={tickets.ticket_list.id.toString()}>
      <th scope="row">{tickets.ticket_list.id}</th>
      <td>{tickets.ticket_list.query}</td>
      <td>{tickets.ticket_list.patient_firstname+' '+tickets.ticket_list.patient_last_name}</td>
      <td>{tickets.ticket_list.assigned_first_name+' '+tickets.ticket_list.assigned_last_name}</td>
      <td>{tickets.ticket_list.ward_name}</td>
    </tr>
  )
}

class tickets extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      totalPages: 0,
      totalRecords: 0
    };
  }

  componentDidMount() {
    this.getAllTickets(1)
  
  }
  getAllTickets=async(page)=>{
    const responseData= await getAllTickets(page);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
              isLoaded: true,
              items: responseData.data.result,
              totalPages: responseData.data.pages,
              totalRecords: responseData.data.count
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  onPageChanged = data => {
    //const { allCountries } = this.state;
   
    const { currentPage, totalPages, pageLimit } = data;
    this.getAllTickets(currentPage)
    console.log(data);
  };

  render() {

    const ticketList = this.state.items; //usersData.filter((user) => user.id < 10)
    const Totrecords = parseInt(this.state.totalRecords); // total users records.
    let pagination;

    if(Totrecords !== 0 ){
      pagination = <Pagination totalRecords={Totrecords} pageLimit={5} pageNeighbours={1} onPageChanged={this.onPageChanged} />
    }

    return (
      <div className="animated fadeIn">
        <Row>
          
          <Col xl={12}>
            <Card>
               <CardHeader>
                 {/* <p>Tickets</p> */}
                <i className="fa fa-align-justify"></i> Tickets 
                {/* <Link to={addticket}><button onClick={this.showHospitalInfo} className='btn btn-success  userbtn'>+ Add ticket</button></Link> */}
              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">Id</th>
                      <th scope="col">Subject</th>
                      <th scope="col">Patient Name</th>
                      <th scope="col">Assignee Name</th>
                      <th scope="col">Ward Name</th>
                    </tr>
                  </thead>
                  <tbody>
                    {ticketList.map((tickets, index) =>
                      <TicketRow key={index} tickets={tickets}/>
                    )}
                  </tbody>
                </Table>
              </CardBody>              
              {pagination}
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default tickets;
